// FONTS
$mazzard: 'Mazzard H', sans-serif;

// COLORS

$black: #000000;
$white: #ffffff;
$blue: #1E90FF;
$text: #373737;
$orange: #F77107;
$grey: #FAFAFA;


// Main
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    &:before,
    &:after {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
  }

html, body {
    margin: 0;
    width: 100%;
    overflow-x: hidden;
}
  
body {
    font-family: $mazzard;
    position: relative;
    /*line-height: 32px;*/
    font-size: 18px;
}
  
ol,
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}
  
a {
    color: inherit;
    text-decoration: none;
    transition: all .2s;
    &:hover,
    &:focus {
      text-decoration: none;
      outline: none;
    }
}

p {
    margin: 0;
    padding: 0;
}

input,
textarea,
button {
    outline: none;
    -webkit-appearance: none;
    &:focus {
      outline: none;
    }
}

textarea {
    resize: none;
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

  


  
