//=====INTRO=========
.quiz-intro {
    background-color: $white;

    &__wrap {
        max-width: 1500px;
        padding: 0 15px;        
        margin: 0 auto;     
        position: relative;
        min-height: 100vh;
    }

    &__header {
        padding: 37px 0 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20vh;
        position: relative;
        z-index: 1;
    }

    &__logo {
        svg {
            width: 230px;
            height: 54px;           
        }

        use {
            fill: $blue;
        }
        
    }

    &__callback {
        font-size: 19px;
        line-height: 145%;
        color: $orange;
        position: relative;
        white-space: nowrap;
        display: inline-block;
        width: 260px;
        padding: 21px 0;
        text-align: center;
        font-weight: 400;
        border: 1px solid $orange;
        border-radius: 50px;

        span {
            position: absolute;
            bottom: -10px;
            left: 0;
            right: 0;
            margin: 0 auto;
            font-size: 17px;
            line-height: 145%;
            background-color: $white;
            width: 190px;
            text-align: center;
        }
    }

    &__help {
        display: none;
        align-items: center;
        border: none;
        background: none;
    }

    &__help-text {
        font-size: 13px;
        line-height: 15px;        
        color: $white;
        margin-right: 15px;
        text-align: left;
        font-family: $mazzard;
    }
    
    &__help-icon {
        display: flex;
        width: 45px;
        height: 45px;
        border-radius: 100px;
        justify-content: center;
        align-items: center;
        background-color: $white;

        svg {
            width: 39px;
            height: 40px;
        }
    }

    &__title {
        font-size: 8vh;
        line-height: 130%;
        margin-bottom: 5vh;
        font-weight: 700;
        color: $text;
        max-width: 668px;
        position: relative;
        z-index: 1;
    }

    &__subtitle {
        font-size: 3vh;
        line-height: 145%;
        font-weight: 400;
        color: $text;
        margin-bottom: 5vh;
        max-width: 658px;
        position: relative;
        z-index: 1;
    }

    &__video {
        position: absolute;
        right: 0;
        bottom: 0;
        margin: auto;
        width: 800px;
        height: 100vh;
        max-height: 800px;
        z-index: 0;
        background-color: $white;

        video {
            background-color: $white;
        }
    }

    &__video-image {
        display: none;

        img {
            max-width: 100%;
        }
    }

    &__row {
        display: flex;
        max-width: 1130px;
        margin-bottom: 80px;
    }

    &__item {
        margin-right: 66px;
        flex-basis: calc(100% / 3);

        &:nth-last-of-type(1) {
            margin-right: 0;
        }
    }

    &__item-icon {
        background-color: $blue;
        width: 80px;
        height: 80px;
        border-radius: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto 35px;
    }

    &__item-text {
        text-align: center;
        font-size: 19px;
        line-height: 145%;
        color: $text;
    }

    &__link {
        font-size: 17px;
        line-height: 145%;
        color: $white;
        font-weight: 400;
        background: linear-gradient(180deg, #F7931A 0%, #F7711A 100%);
        box-shadow: 0px 12px 22px rgba(247, 113, 26, 0.4);
        border-radius: 35px;
        padding: 15px;
        display: block;
        text-align: center;
        max-width: 200px;
        position: relative;
        z-index: 1;
    }
}

//=========STEPS============
.quiz {
    background-color: $grey;
    display: flex;
    align-items: center;
    min-height: 100vh;
    overflow: auto;
    padding: 100px 0;
    flex-wrap: wrap;
    

    &__wrap {
        width: 1270px;
        display: flex;
        margin: 0 auto;
        background: #FFFFFF;
        box-shadow: 0px 10px 30px rgba(104, 104, 104, 0.3);
        border-radius: 10px;     

        &.last-steps {
            .quiz__content {
                max-width: none;
            }

            .quiz__sidebar {
                display: none;
            }

            .quiz__header {
                display: none;
            }

            .quiz__footer {
                display: none;
            }

            .quiz__form {
                padding: 55px 130px;
            }
        }
    }

    &__content {
        width: 100%;
        max-width: 940px;
        // min-width: 940px;
    }

    &__sidebar {
        width: 100%;
        max-width: 330px;
        // min-width: 330px;
        background-color: rgba(238, 238, 238, 0.3);
        padding: 35px 30px;
    }

    &__logo {
        display: none;

        svg {
            width: 106px;
            height: 25px;
        }

        use {
            fill: $white;
        }
    }

    &__help {
        display: flex;
        background: none;
        align-items: center;
        border: none;
        padding: 0;
        font-family: $mazzard;
    }

    &__help-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90px;
        height: 90px;
        min-width: 90px;
        border-radius: 100px;
        background-color: $white;
        margin-right: 20px;

        svg {
            width: 78px;
            height: 78px;
        }
    }

    &__help-text {
        p {
            font-size: 17px;
            line-height: 20px;
            color: $text;
            text-align: left;

            strong {
                font-size: 19px;
                line-height: 22px;
                margin-bottom: 10px;
            }
        }
    }

    &__help-number {
        font-size: 17px;
        line-height: 20px;
        color: $text;
        margin-top: 10px;
        display: none;
    }

    &__header {
        padding: 40px 40px 12px;
        border-bottom: 2px solid #E9E9E9;
    }

    &__header-text {
        font-size: 19px;
        line-height: 145%;
        color: $text;
        padding-left: 25px;
        position: relative;
        

        &:before {
            position: absolute;
            content: "";
            width: 15px;
            height: 15px;
            background-image: url(../img/icons/quiz-header.svg);
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
        }
    }

    &__form {
        padding: 25px 40px 30px;
    }

    &__form-step {
        display: none;
    }

    &__form-step--active {
        display: block;
    }

    &__form-step-title {
        font-size: 34px;
        line-height: 145%;
        color: $text;
        font-weight: 400;
        margin-bottom: 6px;
    }

    &__form-step-attention {
        background: rgba(30, 144, 255, 0.2);
        border: 1.5px solid rgba(30, 144, 255, 0.8);
        border-radius: 5px;
        display: inline-block;
        padding: 6px 10px 6px 38px;
        position: relative;
        font-size: 13px;
        line-height: 140%;
        color: $text;
        margin-bottom: 30px;

        &:before {
            position: absolute;
            content: "";
            width: 18px;
            height: 18px;
            background-image: url(../img/icons/quiz-attention.svg);
            top: 0;
            bottom: 0;
            left: 10px;
            margin: auto;
        }
    }
    
    &__form-progress {
        margin-bottom: 35px;
    }

    &__form-progress-text {
        font-weight: 500;
        font-size: 23px;
        line-height: 145%;
        color: $blue;
    }

    &__form-progress-bar {
        display: flex;
    }

    &__form-progress-bar-item {
        background-color: $blue;
        width: 13.5px;
        height: 15px;
        margin-right: 6px;

        &:nth-last-of-type(1) {
            margin-right: 0;
            width: 16.5px;
            border-top-right-radius: 50px;
            border-bottom-right-radius: 50px;
        }

        &:nth-of-type(1) {
            width: 16.5px;
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
        }
    }

    &__form-row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__form-row-text {
        font-size: 19px;
        line-height: 145%;
        color: $text;
    }

    &__form-row-btn {
        font-size: 17px;
        line-height: 50px;
        text-align: center;
        width: 260px;
        height: 50px;
        font-family: $mazzard;
        color: $white;
        background: linear-gradient(180deg, #F7931A 0%, #F7711A 100%);
        
        border-radius: 35px;
        border: none;
        cursor: pointer;
        transition: 0.2s;

        &:hover {
            box-shadow: 0px 12px 22px rgba(247, 113, 26, 0.4);
        }

        &:active {
            background-color: $orange;
        }
    }

    &__form-step-content {
        display: flex;
        margin: 0 -15px;
    }

    &__form-step-content-col {
        width: 50%;
        margin: 0 15px;
    }

    &__form-step-content-col--map {
        height: 330px;
        padding-right: 10px!important;
 
        .scroll-element.scroll-y.scroll-scrolly_visible {
            width: 3px;
            height: 100%;
        }

        & > .scroll-element .scroll-element_track {
            background-color: #E9E9E9;
        }

        & > .scroll-element .scroll-bar {
            background-color: $blue;
            opacity: 1;
        }
    }

    &__form-label {
        display: block;
        margin-bottom: 30px;
        max-width: 415px;

        &:nth-last-of-type(1) {
            margin-bottom: 0;
        }
    }

    &__form-input {
        background: #FFFFFF;
        border: 2px solid #EDEDED;
        border-radius: 30px;
        padding: 14px 50px;
        font-size: 19px;
        line-height: 145%;
        color: #B6B6B6;
        width: 390px;

        &::placeholder {
            color: #B6B6B6;
        }
        
    }

    &__form-checkbox {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 20px;

        &:checked + .quiz__form-checkbox-text {
            border-color: $blue;
        }

        &:checked + .quiz__form-checkbox-text:before {
            background-color: $blue;
        }

        &:checked + .quiz__form-checkbox-text:after {
            opacity: 1;
        }
    }

    &__form-checkbox-text {
        position: relative;
        padding: 18px 25px 18px 74px;
        cursor: pointer;
        display: inline-block;
        border: 2px solid #EDEDED;
        border-radius: 5px;
        width: 100%;
        max-width: 415px;
        transition: 0.2s;
        font-size: 19px;
        line-height: 1;
        color: $text;

        &:hover {
            box-shadow: 0px 0px 15px rgba(30, 144, 255, 0.4);
            border-color: $blue;

            &:after {
                opacity: 1;
            }
        }

        &:before {
            content: "";
            width: 24px;
            height: 24px;
            background-color: #E9E9E9;
            border-radius: 3px;
            position: absolute;
            left: 25px;
            top: 0;
            bottom: 0;
            margin: auto;
            transition: 0.2s;
        }

        &:after {
            content: "";
            width: 16px;
            height: 12px;
            background-image: url(../img/icons/checkbox.svg);;
            border-radius: 3px;
            position: absolute;
            left: 29px;
            top: 0;
            bottom: 0;
            margin: auto;
            opacity: 0;
            transition: 0.2s;
        }
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        padding: 20px 40px 35px;
        border-top: 2px solid #E9E9E9;
    }

    &__footer-progress-text {
        font-size: 15px;
        line-height: 140%;
        color: $text;
        font-weight: 400;
        margin-bottom: 9px;

        span {
            font-size: 19px;
            line-height: 145%;
            font-weight: 500;
            color: $blue;
        }
    }

    &__footer-progress-bar {
        display: flex;
    }

    &__footer-progress-bar-item {
        background-color: rgba($color: $blue, $alpha: 0.3);
        width: 13.5px;
        height: 15px;
        margin-right: 6px;

        &.active {
            background-color: $blue;
        }

        &:nth-last-of-type(1) {
            margin-right: 0;
            width: 16.5px;
            border-top-right-radius: 50px;
            border-bottom-right-radius: 50px;
        }

        &:nth-of-type(1) {
            width: 16.5px;
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
        }
    }

    &__footer-btns {
        font-size: 0;
        display: flex;
        align-items: center;
        margin-top: 15px;
    }

    &__footer-prev {
        display: none;
        width: 50px;
        height: 50px;
        line-height: 58px;
        text-align: center;
        border-radius: 50px;
        background-color: #F7F7F7;
        border: 2px solid #F7F7F7;
        
        padding: 0;
        margin-right: 30px;
        cursor: pointer;
        transition: 0.2s;

        &:hover {
            border: 2px solid #EDEDED;
        }
        
        
    }

    &__footer-next {
        font-size: 17px;
        line-height: 145%;
        color: $white;
        background: linear-gradient(180deg, #F7931A 0%, #F7711A 100%);
        
        border-radius: 35px;
        border: none;
        padding: 13px 31px;
        font-family: $mazzard;
        cursor: pointer;
        transition: 0.2s;

        &:hover {
            box-shadow: 0px 12px 22px rgba(247, 113, 26, 0.4);
        }
        
        svg {
            margin-left: 11px;
        }
    }

    &__bottom {
        display: none;
        width: 100%;
        max-width: 1500px;
        margin: 0 auto;
        padding: 100px 15px 0;

        &.active {
            display: block;
        }
    }

    &__bottom-logo {
        text-align: center;
        display: block;
        font-size: 19px;
        line-height: 145%;
        margin: 0 auto 52px;
        max-width: 282px;

        

        svg {
            display: block;
            margin: 0 auto 20px;
            width: 230px;
            height: 54px;
        }

        use {
            fill: $blue;
        }
    }

    &__bottom-row {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin: 0 -15px;
    }

    &__bottom-row-item {
        width: calc((100% / 3) - 30px);
        padding: 30px;
        margin: 0 15px 30px;
        display: flex;
        align-items: center;
        margin-bottom: 40px;
        position: relative;
        box-shadow: 0px 2px 19px rgba(0, 0, 0, 0.1);
        border-radius: 10px;

        p {
            font-size: 19px;
            line-height: 145%;
        }

        &:nth-last-of-type(1) {
            width: calc(50% - 30px);
            padding-right: 200px;

            &:after {
                content: "";
                width: 79px;
                height: 86px;
                background-image: url(../img/icons/gift.svg);
                position: absolute;
                right: 75px;
                top: 0;
                bottom: 0;
                margin: auto;
            }
        }
    }

    &__bottom-row-item-icon {
        background-color: $white;
        display: flex;
        border-radius: 100px;
        width: 100px;
        min-width: 100px;
        height: 100px;
        align-items: center;
        justify-content: center;
        margin-right: 30px;

        p {
            font-size: 80px;
            line-height: 145%;
            color: $blue;
            font-weight: 700;

        }
    }
    
    &__bottom-row-item-label {
        position: absolute;
        right: -10px;
        top: -20px;
        font-size: 19px;
        line-height: 20px;
        font-weight: 700;
        background: linear-gradient(270deg, #50CAFF 0%, #0478FF 100%);
        box-shadow: 2px 2px 4px rgba(114, 142, 171, 0.1);
        border-radius: 20px;
        color: $white;
        padding: 10px 25px;
    }
}

