@media screen and (max-width: 1270px) {
    .quiz {
        &__form {
            border-radius: 0;
        }
    }
}

@media screen and (max-width: 1200px) {
    //=====INTRO=========
    .quiz-intro {


        &__wrap {
            max-width: 1500px;
            padding: 0 15px 50px;        
            margin: 0 auto;     
            position: relative;
        }

        &__header {
            margin-bottom: 10vh;
        }

        &__title {
            font-size: 5vw;
            margin-bottom: 5vh;
        }

        &__subtitle {
            font-size: 2.5vw;
            line-height: 145%;          
            margin-bottom: 5vh;
        
        }

        &__video {
            display: none;
        }

        &__video-image {
            display: block;
        }
    }

    //======STEPS=======
    .quiz {
        &__form-step-content {
            flex-wrap: wrap;
            margin: 0;
        }
    
        &__form-step-content-col {
            width: 100%;
            margin: 0 0 15px;

            &:nth-last-of-type(1) {
                margin-bottom: 0;
            }
        }

        &__form-step-content-col--map {
            width: 100%;
            height: auto;
            max-width: 395px;
            order: 2;
            padding-right: 0!important;
            margin-top: 30px;
        }

        &__form-step-content-col--map-image {
            order: 1;
        }

        &__form-label {
            margin-bottom: 15px;
        }


    
        &__bottom-row {
            
            justify-content: flex-start;
        }
    
        &__bottom-row-item {
            width: calc((100% / 2) - 30px);

            p {
                font-size: 16px;
                
            }

            &:nth-last-of-type(1) {
                width: 100%;
            }
            
        }

        &__bottom-row-item-icon {
            p {
                font-size: 80px;
    
            }
        }
    }
}

@media screen and (max-width: 992px) {
    //=====INTRO=========
    .quiz-intro {


        &__wrap {
            max-width: 1500px;
            padding: 0 15px 100px;        
            margin: 0 auto;     
            position: relative;
        }

        &__header {
            padding: 30px;
            background-color: $blue;
            margin: 0 -15px 10vh;
            border-bottom-left-radius: 50px;
        }
    
        &__logo {
            
            svg {
                width: 106px;
                height: 25px;           
            }
    
            use {
                fill: $white;
            }
            
        }
    
        &__callback {
            display: none;
        }
    
        &__help {
            display: flex;
        }

        
    }


    //=======STEPS========
    .quiz {
        padding: 0;
        align-items: flex-start;
        min-height: auto;

        &__wrap {
            flex-wrap: wrap;
            box-shadow: none;
            
            &.last-steps {
                .quiz__form {
                    padding: 55px 15px;
                }

                .quiz__sidebar {
                    display: flex;
                }
            }
        }

        &__content {
            width: 100%;
            max-width: none;
            order: 2;
        }

        &__sidebar {
            order: 1;
            width: 100%;
            max-width: none;
            border-bottom-left-radius: 50px;
            background-color: $blue;
            padding: 28px 40px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            
        }

        &__logo {
            display: block;
        }

        &__help-icon {
            width: 45px;
            height: 45px;
            min-width: 45px;
            margin-left: 15px;
            margin-right: 0;
            order: 2;
    
            svg {
                width: 40px;
                height: 40px;
            }
        }
    
        &__help-text {
            order: 1;
            width: 75px;
            p {
                font-size: 13px;
                line-height: 15px;
                color: $white;

                &:nth-of-type(1) {
                    display: none;
                }
    
                strong {
                    font-size: 15px;
                    line-height: 18px;
                }
            }
        }

        &__help-number {
            display: none!important;
        }

        &__header {
            display: none;
        }

        &__form-row-block {
            width: 100%;

            .quiz__form-label {
                margin: 0 auto 15px;

                &:nth-last-of-type(1) {
                    margin-bottom: 0;
                }
            }
        }

        &__form-input {
            background: #FFFFFF;
            border: none;
            border-bottom: 1px solid #979797;
            border-radius: 0;
            padding: 14px 0;
            font-size: 16px;
            color: #B6B6B6;
            width: 100%;
            max-width: 390px;
        }

        &__form-row-btn--submit {
            margin-top: 40px;
        }

        &__form-progress-text {
            display: none;
        }

        &__form-progress-bar {
            display: flex;
            justify-content: center;
        }

        &__form-row {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
        }

        &__form-row-text {
            width: 100%;
            text-align: center;
            margin-bottom: 40px;
        }

        &__bottom {
            padding: 0 15px 15px;
            background-color: $white;
        }

        &__bottom-logo {
            margin: 0 auto 45px;
        }
    }


    .quiz-thanks {

    
        &__header {
            background-color: $blue;
            margin: 0 -15px 60px;
            border-bottom-left-radius: 50px;
            padding: 30px;
        }
    
        &__logo {
            svg {
                width: 106px;
                height: 25px;           
            }
    
            use {
                fill: $white;
            }
            
        }
    
        &__callback {
            display: none
        }
    
        &__help {
            display: flex;
            
        }

        &__content {
            padding: 70px 15px 200px;
        }
    }
}

@media screen and (max-width: 768px) {
    .quiz {
        &__form-step-title {
            font-size: 18px;
            line-height: 145%;
            margin-bottom: 27px;
        }

       
        &__bottom-row {
            margin: 0 -10px;
        }

        &__bottom-row-item {
            width: calc((100% / 2) - 30px);
            margin: 0 15px 30px;
            background-color: #FAFAFA;
            box-shadow: 0px 2px 19px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            display: block;
            padding: 20px 15px 25px;

            p {
                font-size: 16px;
                
            }
            
        }

        &__bottom-row-item-icon {
            margin: 0 auto 25px 30px;
            position: relative;
            width: 70px;
            height: 70px;
            min-width: 70px;
            p {
                font-size: 70px;
                position: absolute;
                left: -35px;
            }
        }

        &__bottom-row-item-label {
            font-size: 12px;
            line-height: 20px;
            padding: 2px 20px;
            top: -15px
        }
    }
}

@media screen and (max-width: 576px) {
//=====INTRO=========
    .quiz-intro {

        &__wrap {
            padding: 0 15px 50px;
        }

        &__header {
            padding: 30px;
            
            background-color: $white;
            background-image: url(../img/sidebar-bg.png);
            background-position: center right;
            background-size: cover;
            margin: 0 -15px 40px;
            border-bottom-left-radius: 50px;
        }

        &__title {
            font-size: 18px;
            margin-bottom: 20px;
        }

        &__subtitle {
            font-size: 13px;         
            margin-bottom: 20px;
        
        }

        &__video-image {
            display: block;
            text-align: center;

            img {
                max-width: 300px;
            }
        }

        &__link {
            margin: 0 auto;
        }
    
    }


    //======STEPS=======
    .quiz {

        &__wrap {
            
            &.last-steps {
                .quiz__form {
                    padding: 35px;
                }
            }
        }

        &__sidebar {
            padding: 28px 35px;
            background-image: url(../img/sidebar-bg.png);
            background-position: center right;
            background-size: cover;
            background-color: $white;
        }

        &__form {
            padding: 15px 35px 30px
        }

        &__form-progress {
            margin-bottom: 25px;
        }

        &__form-step-content-col--map {
            width: 100%;
            height: auto;
            max-width: none;
            order: 2;
            padding-right: 0!important;
            margin-top: 30px;
        }

        &__form-step-content-col--map-image {
            order: 1;

            img {
                width: 100%;
                max-width: 415px;
            }
        }

        &__form-checkbox-text {
            font-size: 16px;
            line-height: 19px;
            padding: 18px 15px 18px 50px;

            &:before {
                left: 15px;
            }

            &:after {
                left: 19px;
            }
        }

        &__form-row-text {
            font-size: 18px;
        }

        &__form-row-btn {
            width: 220px;
        }

        &__footer {
            display: flex;
            justify-content: center;
            padding: 25px 35px 35px;
            
            flex-wrap: wrap;
        }

        &__footer-progress {
            width: 100%;
            display: flex;
            justify-content: center;
        }
    
        &__footer-progress-text {
            display: none;
        }
    
        &__footer-progress-bar {
            display: flex;
            
        }
    
        
    
        &__footer-btns {
            font-size: 0;
            display: flex;
            align-items: center;
            margin-top: 40px;
            justify-content: space-between;
            width: 100%;
        }
    
        &__footer-prev {
            display: none;
            width: 50px;
            height: 50px;
            line-height: 58px;
            text-align: center;
            border-radius: 50px;
            background-color: #F7F7F7;
            border: 2px solid #F7F7F7;
            
            padding: 0;
            margin-right: 30px;
            cursor: pointer;
            transition: 0.2s;
    
            &:hover {
                border: 2px solid #EDEDED;
            }
            
            
        }
    
        &__footer-next {
            font-size: 17px;
            line-height: 145%;
            color: $white;
            background: linear-gradient(180deg, #F7931A 0%, #F7711A 100%);
            margin-left: auto;
            border-radius: 35px;
            border: none;
            padding: 13px 31px;
            font-family: $mazzard;
            cursor: pointer;
            transition: 0.2s;
    
            &:hover {
                box-shadow: 0px 12px 22px rgba(247, 113, 26, 0.4);
            }
            
            svg {
                margin-left: 11px;
            }
        }

        &__bottom-logo {
            
            margin: 0 auto 45px;        
    
            svg {
                
                width: 152px;
                height: 36px;
            }
        }

        &__bottom-row-item {
            
            
            p {
                font-size: 12px;
                line-height: 14px;
            }
            &:nth-last-of-type(1) {
                width: 100%;
                display: block;
                padding: 20px 15px 25px;
                padding-right: 130px;

                .quiz__bottom-row-item-icon {
                    margin: 0 24px 0 30px;
                }

                &:after {
                    right: 25px;
                }
            }
            
        }

        &__bottom-row-item-icon {
            
            p {
                font-size: 70px;
                position: absolute;
                left: -35px;
            }
        }
    }

    .quiz-thanks {

        &__wrap {
            padding: 0 0 15px;
            background-color: $white;
        }

        &__header {
            display: none;
        }

        &__content {
            padding: 320px 15px 300px;
            box-shadow: none;
            border-bottom-left-radius: 50px;
            background-image: url(../img/thanks-bg.png);
            background-position: center top;
            background-size: 576px auto;
        }

        &__icon {
            display: none;
        }

        &__title {
            margin-bottom: 50px;
            color: $white;
            font-size: 30px;
            line-height: 35px;
        }

        &__text {
            font-size: 16px;
            line-height: 145%;
            color: $white;
        }

        &__social {
            display: flex;
        }
    }

}